.about {
  font-family: $font;
  background-color: $delivery-background;

  & .wrapper {
    width: 85%;
    margin: 0 auto;
    max-width: 1440px;

    & .content {

      & h1 {
        margin: 0;
        padding: 65px 0 18px;
        font-size: 46px;
        font-weight: 600;
        letter-spacing: 0.6px;

        @media (max-width: 600px) {
            font-weight: bold;
            font-size: 20px;
            line-height: 26px;
            padding: 20px 0 0px;
        }
      }

      & p {

        line-height: 24px;

        @media (min-width: 600px) {
          width: 63%;
        }

        @media (max-width: 600px) {
          font-size: 14px;
        }
      }

      & ul {
        color: $black;
        margin: 30px 0 30px;
        padding-left: 17px;
        margin-top: 35px;

        & li {
          list-style: none;
        }

        li:before {
          content: "";
          display: inline-block;
          background-color: $orange;
          font-weight: 600;
          margin: 0 15px 1px 0;
          width: 9px;
          height: 9px;
          border-radius: 9px;
        }
      }

      & ol {
        color: $black;
        padding: 0 0 50px 15px;
        counter-reset: myCounter;
        margin: 36px 0 2px;


        & li {
          list-style: none;
        }

        & li:before {
          counter-increment: myCounter;
          content: counter(myCounter)".";
          color: $orange;
          display: inline-block;
          text-align: right;
          margin: 0 13px 0 0;
          font-weight: 600;
        }
      }

      & li+li {
        padding: 13px 0 0;
      }
    }
  }
}
