.popup {
    position: fixed;
    width: 100vw;
    height: 100vh;
    background: rgba(0, 0, 0, 0.8);
    top: 0;
    left: 0;
    z-index: 100;
    padding: 23px 10px;
    overflow: auto;
}

.popup__header {
    margin-top: 0;
    margin-bottom: 16px;
    font-weight: bold;
    font-size: 46px;
    line-height: 61px;

    @media(max-width: 767px) {
        font-size: 30px;
        line-height: 35px;
    }
}

.popup__description {
    font-size: 16px;
    line-height: 21px;
    display: block;
    margin-bottom: 16px;
}

.popup__time {
    display: block;
    font-size: 16px;
    line-height: 21px;
    color: #A5A5A5;
    margin-bottom: 30px;

    & b {
        color: black;
        margin-left: 10px;
    }
}

.popup__block {
    width: 100%;
    display: flex;
    flex-direction: column;
    border-radius: 12px;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    background-color: white;
    max-width: 537px;
    padding: 50px 70px;

    @media(max-width: 767px){
        padding: 30px;
        max-width: 440px;
    }
}

.popup .button {
    font-family: $font;
    min-height: 60px;
    max-width: 250px;
    font-weight: bold;
    font-size: 16px;
    line-height: 21px;

    @media(max-width: 767px){
        max-width: 100%;
    }
}

