.main-catalog {
  &__container {
    padding-top: 40px;
    padding-bottom: 30px;

    @media (max-width: 1500px) {
      padding-left: 30px;
      padding-right: 30px;
    }

    @media (max-width: 767px) {
      padding-left: 20px;
      padding-right: 20px;
    }

    @media (max-width: 600px) {
      padding-top: 20px;
      padding-bottom: 20px;
    }
  }

  & h2 {
    margin: 0;
    margin-bottom: 30px;
    font-size: 32px;
    line-height: 42px;
    color: #494949;

    @media (max-width: 600px) {
      margin-bottom: 20px;
      font-size: 20px;
      line-height: 26px;
    }
  }

  &__list {
    @include list-reset;
    display: flex;
    flex-wrap: wrap;
    margin-left: -30px;
  }

  &__item {
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: center;
    width: calc(100% / 4 - 30px);
    padding: 30px 40px;
    margin-left: 30px;
    margin-bottom: 30px;
    background-color: #ffffff;
    border-radius: 46px;

    @media (max-width: 1440px) {
      width: calc(100% / 3 - 30px);
    }

    @media (max-width: 1100px) {
      width: calc(100% / 2 - 30px);
    }

    @media (max-width: 767px) {
      flex-direction: row;
      width: calc(100% - 30px);
      margin-bottom: 10px;
      padding: 10px;
      border-radius: 12px;
    }

    & img {
      width: 248px;
      height: 197px;

      @media (max-width: 600px) {
        width: 110px;
        height: 75px;
        transform: translateX(-10px) rotate(-90deg);
      }
    }

    &--promo {
      &::before {
        content: "";
        position: absolute;
        top: 16px;
        right: 16px;
        z-index: 1;
        width: 68px;
        height: 68px;
        background-color: $orange;
        border-radius: 50%;
        background-image: url("../img/gift-white.svg");
        background-position: center;
        background-size: 34px 34px;
        background-repeat: no-repeat;

        @media (max-width: 600px) {
          top: 10px;
          left: 10px;
          width: 34px;
          height: 34px;
          background-size: 17px 17px;
        }
      }
    }
  }

  &__text {
    width: 100%;

    & a {
      font-size: 18px;
      line-height: 24px;
      font-weight: bold;
      text-decoration: none;
      transition: opacity 0.3s;

      @media (max-width: 600px) {
        font-size: 14px;
        line-height: 18px;
      }

      &:hover {
        opacity: 0.8;
      }
    }

    & p {
      max-width: 228px;
      margin: 0;
      margin-top: 6px;
      margin-bottom: 10px;
      font-size: 14px;
      line-height: 18px;
      color: #AFAFAF;

      @media (max-width: 600px) {
        font-size: 12px;
        line-height: 16px;
      }
    }

    & span {
      font-size: 14px;
      line-height: 18px;
      color: $orange;

      @media (max-width: 600px) {
        font-size: 12px;
        line-height: 16px;
      }
    }
  }

  span.main-catalog__price {
    font-weight: bold;
    font-size: 36px;
    line-height: 47px;
    color: $black;

    @media (max-width: 600px) {
      font-size: 24px;
      line-height: 32px;
    }
  }

  span.main-catalog__price--new {
    font-size: 24px;
    line-height: 32px;
    color: $orange;

    @media (max-width: 600px) {
      font-size: 14px;
      line-height: 18px;
    }
  }

  span.main-catalog__price--old {
    align-self: flex-end;
    font-size: 14px;
    line-height: 18px;
    color: #B7B7B7;
    text-decoration: line-through;

    @media (max-width: 600px) {
      font-size: 12px;
      line-height: 16px;
    }
  }

  &__price-wrapper {
    display: flex;
    width: 100%;
    margin-top: 27px;

    @media (max-width: 600px) {
      margin-top: 6px;
    }
  }

  &__button {
    width: 143px;
    height: 45px;
    margin-left: auto;
    font-size: 16px;
    line-height: 21px;
    color: #ffffff;
    transition: opacity 0.3s;

    @media (max-width: 600px) {
      width: 90px;
      height: 32px;
      border-radius: 6px;
      font-size: 11px;
      line-height: 15px;
    }

    &:focus {
      outline: none;
    }

    &.hidden {
      display: none;
    }
  }

  &__form {
    display: none;
    margin-left: auto;

    @media (max-width: 600px) {
      position: absolute;
      top: 14px;
      right: 14px;
      flex-direction: column-reverse;
    }

    &.active {
      display: flex;
    }
  }
}
