.order {
    padding: 34px 20px 70px 20px;

    * {
        outline: none;
    }

    &__wrapper {
        max-width: 1490px;
        margin: 0 auto;
    }

    & h1 {
        font-size: 46px;
        line-height: 61px;
        padding: 0 30px;
        margin-bottom: 30px;

        @media(max-width: 767px) {
            font-size: 20px;
            line-height: 20px;
            margin-bottom: 20px;
            padding: 0 10px;
        }
    }

    &__part {
        background: $white;
        border-radius: 12px;
        padding: 30px;
        margin-bottom: 30px;
        position: relative;
        z-index: 2;

        @media(max-width: 767px) {
            padding: 20px;
            margin-bottom: 20px;
        }

        &:first-child {
            position: relative;

            & div {
                display: flex;
                flex-wrap: wrap;
                justify-content: space-between;

                @media(max-width: 767px) {
                    flex-direction: column;
                }
            }

            & span {
                position: absolute;
                width: 200px;
                border-top: 4px solid $orange;
                border-radius: 12px;
                background: white;
                font-size: 14px;
                line-height: 18px;
                color: #6B6B6B;
                top: -45px;
                right: 15px;
                padding: 20px 20px 25px 20px;
                box-shadow: 0 10px 10px rgba(0, 0, 0, 0.1);

                &::before {
                    content: "";
                    position: absolute;
                    bottom: -14px;
                    right: 90px;
                    width: 21px;
                    height: 15px;
                    background: url('../img/sprite/notion-white.svg') no-repeat center center;
                    transform: rotate(180deg);
                }

                @media(max-width: 767px) {
                    display: none;
                }
            }

            & input {
                padding: 0 50px 0 25px;
                width: 44.2%;

                @media(max-width: 767px) {
                    background: none;
                    width: 100%;
                }
                    &:first-child {
                    width: 53.4%;

                    @media(max-width: 767px) {
                         width: 100%;
                         margin-bottom: 10px;
                    }
                }
            }
        }

        &--adress {
            display: flex;
            flex-wrap: wrap;
            justify-content: space-between;
            padding-bottom: 0;
            border-top-left-radius: 0;

            @media(max-width: 767px) {
                flex-direction: column;
            }

            & div:last-child {
                width: 100%;
                display: flex;
                align-items: center;

                & label {
                    margin-bottom: 0;

                    @media(max-width: 767px) {
                        margin-bottom: 15px;
                    }
                }

                @media(max-width: 767px) {
                    order: 1;
                    flex-direction: column;
                    align-items: flex-start;
                }
            }

            & div:first-child {
                width: 53.4%;
                display: flex;
                flex-wrap: wrap;
                justify-content: space-between;

                @media(max-width: 767px) {
                    width: 100%;
                    order: -1;
                }

                & input {
                    // max-width: 102px;
                    width: 22%;
                    margin-bottom: 20px;
                    padding: 0 15px;

                    &:first-child {
                        max-width: 100%;
                        width: 100%;
                        
                    }
                }
            }

            & div:nth-child(2) {
                width: 44.2%;

                @media(max-width: 767px) {
                    width: 100%;
                    order: 2;
                }

                & textarea {
                    width: 100%;
                    height: calc(100% - 20px);
                    margin-bottom: 20px;
                    resize: none;
                    padding: 20px;

                    @media(max-width: 767px) {
                        width: 100%;
                        min-height: 117px;
                    }
                }
            }
        }

        &--no-delivery {
            & div {
                display: flex;
                align-items: center;

                & img {
                    margin-right: 30px;
                }

                & div {
                    flex-direction: column;
                    align-items: flex-start;

                    & span:first-of-type {
                        font-weight: bold;
                        font-size: 18px;
                        line-height: 24px;
                        margin-bottom: 6px;
                    }

                    & span:last-of-type {
                        font-size: 14px;
                        line-height: 18px;
                        color: #AFAFAF;
                        margin-bottom: 12px;
                    }

                    & a {
                        text-decoration: none;
                        font-size: 14px;
                        line-height: 18px;
                        color: $orange;
                    }
                }
            }
        }

        &:last-child {
            display: flex;
                flex-wrap: wrap;

                & h3 {
                    width: 100%;
                    margin-bottom: 30px;

                    @media(max-width: 767px) {
                        margin-bottom: 20px;
                    }
                }

                & div {
                    width: 100%;
                    max-width: 275px;
                }
        }

        & label {
            padding-top: 5px;
            font-size: 16px;
            line-height: 21px;
            color: #1A1A1A;
            padding-left: 42px;
            padding-right: 10px;
            margin-bottom: 30px;
            display: flex;
            align-items: center;
            position: relative;

            @media(max-width: 767px) {
                font-size: 14px;
                line-height: 18px;
                margin-bottom: 16px;
            }

            & img {
                margin: 0 10px 0 5px;
            }

            &::before {
                content: "";
                position: absolute;
                width: 28px;
                height: 28px;
                border-radius: 50%;
                border: 1px solid $orange;
                left: 0;
                top: 1px;

                @media(max-width: 767px) {
                    width: 24px;
                    height: 24px;
                }
            }
        }

        & input[type=radio]:checked + label::after {
            content: "";
            position: absolute;
            width: 12px;
            height: 12px;
            border-radius: 50%;
            background: $orange;
            left: 8px;
            top: 9px;

            @media(max-width: 767px) {
                top: 7px;
                left: 6px;
            }
        }
    }

    & form {
        width: 100%;
        display: flex;
        justify-content: space-between;

        @media(max-width: 1280px) {
            flex-direction: column;
        }

        
    }

    & input,textarea {
        border: 1px solid rgba(0, 0, 0, 0.12);
        min-height: 60px;
        border-radius: 12px;
        font-size: 16px;
        line-height: 21px;
        font-family: $font;
        outline: none;

        @media(max-width: 767px){
            min-height: 56px;
            font-size: 14px;
            line-height: 16px;
        }

        &:active {
            border-color: $orange;
        }

        &:focus {
            border-color: $orange;
        }
        
        &::-webkit-input-placeholder  {
            font-size: 14px;
            line-height: 18px;
            color: #AFAFAF;

            @media(max-width: 767px){
                min-height: 56px;
                font-size: 14px;
                line-height: 16px;
            }
        }
    
        &::-moz-placeholder {
            font-size: 14px;
            line-height: 18px;
            color: #AFAFAF;

            @media(max-width: 767px){
                font-size: 12px;
                line-height: 16px;
            }
        }
    
        &:-ms-input-placeholder {
            font-size: 12px;
            line-height: 18px;

            @media(max-width: 767px){
                font-size: 12px;
                line-height: 16px;
            }
        }
    }

    &__right-side {
        position: relative;
        width: 100%;
        max-width: 497px;
        background: $white;
        border-radius: 12px;
        padding: 40px;
        align-self: flex-start;
        padding-bottom: 115px;
        margin-top: -80px;

        @media(max-width: 1280px) {
            width: 100%;
            max-width: 100%;
            margin-top: 0;
            padding: 15px;
            padding-bottom: 115px;
        }
        
        & h2 {
            margin-top: 5px;
            font-size: 36px;
            line-height: 36px;
            margin-bottom: 37px;

            @media(max-width: 1280px) {
                display: none;
            }
        }

        & ul {
            @include list-reset;

            & li {
                margin-bottom: 22px;
            }
        }
    }

    &__left-side {
        width: 100%;
        max-width: 917px;
        margin-right: 30px;

        @media(max-width: 1280px) {
            width: 100%;
            max-width: 100%;
            margin-right: 0;
        }

        & h3 {
            font-size: 24px;
            line-height: 32px;
            margin-top: 0;
            width: 100%;
            margin-bottom: 30px;

            @media(max-width: 767px) {
                font-size: 16px;
                line-height: 21px;
            }
        }
    }

    &__item {
        border-bottom: 1px solid rgba(0, 0, 0, 0.1);
        position: relative;

        &-description {
            display: flex;

            & img {
                flex-shrink: 0;
                align-self: flex-start;
                margin-right: 16px;

                @media(max-width: 767px) {
                    transform: rotate(90deg);
                    margin-left: -20px;
                    margin-top: 10px;
                    margin-right: 0;
                }
            }

            & h3 {
                font-size: 18px;
                line-height: 24px;
                align-items: flex-start;
                max-width: 240px;
                margin-top: 8px;
                margin-bottom: 8px;

                @media(max-width: 767px) {
                    font-size: 14px;
                    line-height: 18px;
                    margin-bottom: 4;
                }
            }

            & p {
                font-size: 14px;
                line-height: 18px;
                color: #AFAFAF;
                max-width: 240px;
                margin-top: 0;

                @media(max-width: 767px) {
                    font-size: 12px;
                    line-height: 16px;
                }
            }
        }

        &-price {
            display: flex;
            justify-content: space-between;
            align-items: center;
            padding-bottom: 18px;

            & span:last-child {
                font-weight: bold;
                font-size: 24px;
                line-height: 32px;

                @media(max-width: 767px) {
                    font-size: 20px;
                    line-height: 24px;
                }
            }

            @media(max-width: 767px){
                margin-top: -20px;
            }
        }

        & button:not(.count-form__btn) {
            position: absolute;
            top: 10px;
            right: 0;
            box-shadow: none;
            padding: 0;
            background: transparent;
            border: none;
        }

    }

    &__counter {
        width: 105px;
        height: 27px;
        margin-top: 5px;

        @media(max-width: 767px) {
            margin-left: 90px;
        }
        
        & .count-form__btn {
            width: 27px;
            height: 27px;
            font-size: 14px;
            display: flex;
            align-items: center;
            justify-content: center;
            border: 1px solid rgba(0, 0, 0, 0.1);
            border-radius: 50%;
            font-family: $font;
        }

        & input {
            border: none;
            min-height: 27px;
            font-size: 14px;
            line-height: 18px;
            color: #AFAFAF;
        }

    }

    &__attention {
        position: absolute;
        bottom: 0;
        left: 0;
        width: 100%;
        min-height: 80px;
        background: #F26C33;
        border-radius: 0px 0px 12px 12px;
        color: white;
        font-size: 14px;
        line-height: 18px;
        text-align: center;
        display: flex;
        justify-content: center;
        align-items: center;

        & p {
            max-width: 320px;
        }

        &::before {
            content: "";
            position: absolute;
            top: -14px;
            right: 15%;
            width: 21px;
            height: 15px;
            background: url('../img/sprite/notion.svg') no-repeat center center;
        }
    }

    &__total {
        display: flex;
        justify-content: space-between;
        align-items: flex-end;

        @media(max-width: 767px) {
            flex-direction: column-reverse;
            align-items: flex-start;
        }

        & button {
            max-width: 250px;
            width: 100%;
            min-height: 60px;

            &:disabled {
                background: #DFDFDF;
            }

            @media(max-width: 767px) {
                max-width: 100%;
            }
        }

        & .button--normal {
            background: $orange;
            border-radius: 12px;
        }

        & p {
            margin: 0;

            @media(max-width: 767px) {
                margin-bottom: 20px;
            }
            & span {
                display: block;

                &:first-child {
                    font-size: 14px;
                    line-height: 18px;
                    text-align: right;
                    color: #AFAFAF;

                    @media(max-width: 767px) {
                        text-align: left;
                    }
                }

                &:last-child {
                    text-align: right;
                    font-weight: bold;
                    font-size: 36px;
                    line-height: 47px;

                    @media(max-width: 767px) {
                        text-align: left;
                    }
                }
            }
        }
    }

    &__tabs {
        display: flex;

        & a {
            background: #FFFFFF;
            border-radius: 12px 12px 0 0;
            text-decoration: none;
            font-weight: bold;
            font-size: 24px;
            line-height: 32px;
            color: rgba(0, 0, 0, 0.2);
            padding: 20px 30px 10px 30px;

            @media(max-width: 767px) {
                font-size: 16px;
                line-height: 21px;
                padding: 14px 20px 0 20px;
            }
        }

        & .order__tabs-active {
            box-shadow: 4px 0px 10px rgba(0, 0, 0, 0.1);
            opacity: 1;
            color: black;
            position: relative;
            z-index: 1;
        }
    }

    &__time {
        padding-top: 20px;
        padding-bottom: 20px;

        @media(max-width: 767px) {
            padding-top: 0;
        }

        & div:first-child{
            max-width: 250px;
        }

        & div:last-child {
            align-items: center;

            & label {
                min-width: 230px;
            }

            @media(max-width: 767px) {
                align-items: flex-start;

                & div {
                    flex-direction: row;
                }
            }
        }

        & select {
            width: 80px;
        }
    }

    .select2-container {
        text-align: center;
        min-height: 60px;
        width: 100px;
    }

    .select2-selection {
        min-height: 60px;
    }

    .select2-container--default .select2-selection--single .select2-selection__rendered {
        min-height: 60px;
        display: flex;
        justify-content: center;
        align-items: center;
        font-size: 16px;
        line-height: 21px;
        color: $black;
        font-family: $font;
    }

    .select2-container--default .select2-selection--single .select2-selection__arrow {
        top: 18px;
        right: 15px;
    }

    .select2:first-of-type .select2-selection--single {
        border-color: rgba(0, 0, 0, 0.12);
        border-radius: 12px 0 0 12px;
        border-right: 0;
    }

    .select2:last-of-type .select2-selection--single {
        border-color: rgba(0, 0, 0, 0.12);
        border-radius: 0 12px 12px 0;
    }

    &__input-false {
        background: url('../img/sprite/order-false.svg') no-repeat calc(100% - 14px) center;
    }

    &__input-true {
        background: url('../img/sprite/order-true.svg') no-repeat calc(100% - 14px) center;
    }
}