.header {
   position: relative;
    background-color: $white;
    color: $menu-color;
    padding: 20px 15px;

    @media (max-width: 1024px) {
      padding: 14px 30px;
    }

    & button {
        margin-right: 10px;
        min-width: 160px;

        &:focus {
          outline: none;
        }
    }

    &__wrapper {
        display: flex;
        align-items: center;
    }

    & nav {
        display: flex;
        align-items: center;
        margin-right: 40px;

        @media (max-width: 1300px) {
          margin-right: 0;
        }

        @media (max-width: 1024px) {
          display: none;
        }
    }

    &__menu {
        @include list-reset;
        display: flex;

        & li {
            margin-left: 30px;

            @media (max-width: 1200px) {
              margin-left: 10px;
            }
        }

        & a {
            font-size: 18px;
            line-height: 24px;
            text-decoration: none;
            white-space: nowrap;

            &:hover {
                opacity: 0.8;
            }
        }
    }

    &__logo {
        margin-left: auto;
        margin-right: auto;

        @media (max-width: 1024px) {
          margin-left: 0;
          margin-right: 24px;

          & img {
            width: 44px;
            height: 35px;
          }
        }
    }

    &__contacts {
        margin-right: 60px;

        @media (max-width: 1200px) {
          margin-right: 20px;
        }

        & span {
            display: block;
            font-size: 14px;
            line-height: 18px;
            color: $menu-time;

            @media (max-width: 1024px) {
              font-size: 11px;
              line-height: 15px;
            }
        }

        & a {
            text-decoration: none;
            font-weight: bold;
            font-size: 18px;
            line-height: 24px;
            color: $black;

            @media (max-width: 1024px) {
              font-size: 15px;
              line-height: 20px;
              color: #8C8C8C;
            }

            &:hover {
                opacity: 0.8;
            }
        }
    }

    &__cart {
        display: flex;
        align-items: center;

        @media (max-width: 1024px) {
          display: none;
        }

        & div {
            min-width: 80px;
            & span {
                display: block;

                &:first-child {
                    font-size: 14px;
                    line-height: 18px;
                    color: $orange;
                }

                &:last-child {
                    font-weight: bold;
                    font-size: 18px;
                    line-height: 24px;
                    color: #000000;
                }
            }
        }

        & a {
            margin-left: 20px;
            min-width: 200px;

            @media (max-width: 1200px) {
              min-width: 160px;
              margin-left: 10px;
            }
        }
    }

    &__mobile-cart {
      // position: absolute;
      top: 10px;
      left: 30px;
      display: none;
      color: $white;
      border: 2px solid $white;

      @media (max-width: 1024px) {
        display: flex;
      }

    }

    & button.header__mobile-button {
      @include btn-reset;
      display: none;
      min-width: 37px;
      margin-left: auto;
      margin-right: 0;

      @media (max-width: 1024px) {
        display: block;
      }
    }

    &__submenu {
      display: none;
      position: absolute;
      top: 0;
      z-index: 5;
      width: 536px;
      padding-top: 30px;
      background-color: $orange;
      border-bottom-right-radius: 10px;
      border-bottom-left-radius: 10px;

      &.is-open {
        display: block;
      }

      & h3 {
        margin: 30px;
        font-size: 30px;
        line-height: 23px;
        color: $white;
      }
    }

    &__submenu-list {
      @include list-reset;

      & li {
        border-top: 1px solid #FC8B5B;
      }

      & a {
        position: relative;
        display: flex;
        justify-content: space-between;
        align-items: center;
        width: 100%;
        padding: 20px 40px 20px 30px;
        font-size: 20px;
        line-height: 26px;
        text-decoration: none;
        color: $white;
        transition: opacity 0.3s;

        &:hover {
          opacity: 0.8;
        }

        &::after {
          content: "";
          flex-shrink: 0;
          display: block;
          width: 8px;
          height: 10px;
          background-image: url("../img/sprite/arrow-next.svg");
          background-repeat: no-repeat;
          background-position: center;
          background-size: contain;
        }
      }
    }

    & button.header__mobile-close {
      @include btn-reset;
      display: none;
      justify-content: center;
      align-items: center;
      min-width: 40px;
      margin-left: auto;
      margin-right: 20px;
    }

    
  &--cart {

    nav {
      color: black;
    }
    .header__button {
      display: none;
    }

    .header__logo {
      order: -1;
      margin-left: 0;
      margin-right: 8%;

      @media(max-width: 1024px) {
        margin-right: 24px
      }
    }

    .header__contacts {
      order: 2;
      margin-right: 0;
      margin-left: 5.5%;

      @media(max-width: 1024px) {
        order: 0;
        margin-left: 0;
      }
    }

    .header__cart {
      margin-left: auto;

      & .button {
        background: $orange;
        color: white;
      }
    }
  }
}

.header.is-open {
  @media (max-width: 1024px) {
    & nav {
      position: fixed;
      top: 0;
      left: 0;
      z-index: 10;
      width: 100%;
      height: 100%;
      display: flex;
      flex-direction: column;
      padding-bottom: 30px;
      background-color: $orange;
      overflow-y: auto;
    }

    & button.header__button {
      display: none;
    }

    & button.header__submenu-button {
      display: none;
    }

    & .header__submenu {
      position: static;
      display: block;
      width: 100%;
      border-radius: 0;
    }

    & .header__menu {
      display: flex;
      flex-direction: column;
      width: 100%;
      margin-bottom: 30px;
      background-color: $orange;

      & li {
        margin-left: 0;
        border-top: 1px solid #FC8B5B;

        &:last-child {
          border-bottom: 1px solid #FC8B5B;
        }
      }

      & a {
        position: relative;
        display: flex;
        justify-content: space-between;
        align-items: center;
        width: 100%;
        padding: 20px 40px 20px 30px;
        font-size: 20px;
        line-height: 26px;
        text-decoration: none;
        color: $white;
        transition: opacity 0.3s;

        &::after {
          content: "";
          flex-shrink: 0;
          display: block;
          width: 8px;
          height: 10px;
          background-image: url("../img/sprite/arrow-next.svg");
          background-repeat: no-repeat;
          background-position: center;
          background-size: contain;
        }
      }
    }

    & .header__mobile-close {
      display: flex;
    }
  }

}
