.about-slider {
  width: 63%;
  padding: 0 0 70px 0;
  margin: 0 0 0 13px;
  max-width: 1440px;
  
  @media (max-width: 600px) {
    width: 100%;
    padding-bottom: 50px;
    margin: 0;
  }
}

.about-slider__container {
  & .swiper-slide {
    width: 100%;
    border-radius: 6px;
    overflow: hidden;


    & img {
      display: block;
      width: 100%;
      height: auto;
      object-fit: cover;
      object-position: center;
    }
  }

  & .swiper-wrapper {
    @include list-reset;
  }

  & .swiper-slide-prev,
  & .swiper-slide-next {
    opacity: 0.2;
  }
}

.about-slider__btn {
  @include btn-reset;
  position: absolute;
  top: 50%;
  z-index: 10;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 47px;
  height: 47px;
  transform: translateY(-50%);
  background-color: rgba(255, 255, 255, 0.3);
  border-radius: 50%;
  border: 1px solid $white;
  transition: opacity 0.3s;
  cursor: pointer;

  @media (max-width: 767px) {
    width: 29px;
    height: 29px;

    & svg {
      width: 8px;
    }
  }

  &--prev {
    left: calc((100% - 71%) / 2 - 60px - 40px);
    transform: translateY(-50%) rotate(180deg);

    @media (max-width: 1024px) {
      left: 20px;
    }
  }

  &--next {
    right: calc((100% - 71%) / 2 - 60px - 40px);

    @media (max-width: 1024px) {
      right: 20px;
    }
  }

  &:focus {
    outline: none;
  }

  &:hover {
    opacity: 0.8;
  }
}
