.button {
    text-decoration: none;
    box-shadow: none;
    background-color: $orange;
    border: none;
    border-radius: 12px;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;

    &:hover {
        opacity: 0.8;
    }

    &--normal {
        font-weight: bold;
        font-size: 16px;
        line-height: 21px;
        max-width: 143px;
        color: $white;
    }

    &--menu {
        font-weight: normal;
        font-size: 18px;
        line-height: 24px;
        max-width: 160px;
        min-height: 54px;
        color: $white;

        & img {
            margin-right: 12px;
        }
    }

    &--border {
        background-color: transparent;
        color: $orange;
        max-width: 200px;
        min-height: 54px;
        font-weight: bold;
        font-size: 16px;
        line-height: 21px;
        border: 2px solid $orange-border;
    }

    &--apple {
        background: $black;
        font-size: 14px;
        line-height: 18px;
        color: $white;
         & img {
             margin-right: 9px;
             margin-top: -3px;
         }
    }

    &--google {
        background: $green;
        font-size: 14px;
        line-height: 18px;
        color: $white;

         & img {
             margin-right: 10px;
         }
    }
}