.main-slider {
  padding: 40px 0;

  @media (max-width: 500px) {
    padding: 0;
  }
}

.main-slider__container {
  & .swiper-slide {
    width: 62%;
    border-radius: 24px;
    overflow: hidden;

    @media (max-width: 1024px) {
      width: 100%;
      border-radius: 0;
    }

    & img {
      display: block;
      width: 100%;
      height: auto;
      object-fit: cover;
      object-position: center;

      @media (max-width: 500px) {
        width: auto;
        height: 219px;
        border-radius: 0;
      }
    }
  }

  & .swiper-wrapper {
    @include list-reset;
  }

  & .swiper-slide-prev,
  & .swiper-slide-next {
    opacity: 0.2;
  }
}

.main-slider__btn {
  @include btn-reset;
  position: absolute;
  top: 50%;
  z-index: 10;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 66px;
  height: 66px;
  transform: translateY(-50%);
  background-color: rgba(94, 94, 94, 0.4);
  border-radius: 50%;
  transition: opacity 0.3s;
  cursor: pointer;

  @media (max-width: 767px) {
    width: 29px;
    height: 29px;

    & svg {
      width: 4px;
    }
  }

  &--prev {
    left: calc((100% - 62%) / 2 - 60px - 40px);
    transform: translateY(-50%) rotate(180deg);

    @media (max-width: 1024px) {
      left: 20px;
    }
  }

  &--next {
    right: calc((100% - 62%) / 2 - 60px - 40px);

    @media (max-width: 1024px) {
      right: 20px;
    }
  }

  &:focus {
    outline: none;
  }

  &:hover {
    opacity: 0.8;
  }
}
