footer {
  font-family: $font;

  & .top {
      background-color: $grey;
      color: $white;
      padding: 0px 15px;

      @media (max-width: 1024px) {
        padding: 0px 30px;
        }

      & .wrapper {
          display: flex;
          justify-content: space-between;
          align-items: center;
          height: 120px;
          margin: 0 auto;

          @media (max-width: 1100px) {
              height: 120px;
          }

          & ul {
              display: flex;
              list-style: none;
              margin: 0;
              padding: 0;
              font-size: 18px;

              @media (max-width: 1100px) {
                  flex-direction: column;
                  font-size: 16px;
              }

              & a {
                text-decoration: none;
                transition-duration: 0.4s;

                &:hover {
                    opacity: 0.8; 
                }
              }

              & li+li {
                  padding-left: 30px;

                  @media (max-width: 1100px) {
                      padding-left: 0;
                      padding-top: 5px;
                  }
              }
          }

          & img {
              width: 102px;

              @media (max-width: 500px) {
                  display: none;
              }
          }

          & .info {
              text-align: end;
              width: 391px;

              @media (max-width: 1100px) {
                  width: 180px;
              }

              & p {
                  font-size: 14px;
                  margin: 0 0 2px;

                  @media (max-width: 500px) {
                      font-size: 11px;
                  }
              }

              & a {
                  font-weight: 600;
                  font-size: 18px;
                  text-decoration: none;
                  transition-duration: 0.4s;

                &:hover {
                    opacity: 0.8; 
                }
              }
          }
      }
  }

  & .bottom {
      background-color: $dark-grey;
      padding: 0px 15px;
      
      @media (max-width: 1024px) {
        padding: 0px 30px;
        }

      & .wrapper {
          display: flex;
          justify-content: space-between;
          align-items: center;
          height: 50px;
          margin: 0 auto;
          color: $light-grey;
          font-size: 14px;
          line-height: 20px;

          @media (max-width: 550px) {
              height: 78px;
          }

          & p {
              @media (max-width: 500px) {
                  font-size: 11px;
                  width: 115px;
              }
          }

          & a {
            text-decoration: none;
            transition-duration: 0.4s;

                &:hover {
                    opacity: 0.8; 
                }
            
              @media (max-width: 500px) {
                  font-size: 14px;
                  width: 115px;
              }
          }

          & .left {
              color: $yellow;
              font-weight: 600;
          }

          & .right {
              color: $orange;
              font-weight: 600;
          }

          & .end {
              text-align: end;
          }
      }
  }
}