.count-form {
  display: flex;
  justify-content: space-between;
  width: 128px;
  height: 47px;

  @media (max-width: 600px) {
    width: 32px;
    height: 112px;
  }

  & input {
    width: 34px;
    border: none;
    background-color: transparent;
    text-align: center;
    outline: none;

    @media (max-width: 600px) {
      width: 32px;
      font-size: 20px;
    }
  }

  &__btn {
    @include btn-reset;
    cursor: pointer;
    width: 47px;
    height: 47px;
    border-radius: 50%;
    border: 1px solid rgba(0, 0, 0, 0.12);
    transition: opacity 0.3s;

    @media (max-width: 600px) {
      width: 32px;
      height: 32px;
    }

    &--plus {
      border: 1px solid $orange;

      @media (max-width: 600px) {
        color: $orange;
      }
    }

    &:focus {
      outline: none;
    }

    &:hover {
      opacity: 0.8;
    }
  }
}
