.catalog-menu {
  background-color: #ffffff;
  overflow: hidden;

  @media (max-width: 767px) {
    position: relative;

    &::after {
      content: "";
      position: absolute;
      top: 0;
      right: -11px;
      width: 11px;
      height: 100%;
      box-shadow: -14px 0 40px rgba(0, 0, 0, 0.6);
    }
  }

  &__container {
    padding-top: 32px;
    padding-bottom: 32px;

    width: 100%;
    overflow-x: auto;
    -ms-overflow-style: none;
    scrollbar-width: none;

    &:-webkit-scrollbar {
      width: 0;
    }

    & .simplebar-scrollbar::before {
      background: rgba(0, 0, 0, 0.12);
    }

    @media (max-width: 767px) {
      position: relative;
      padding-top: 26px;
      padding-bottom: 26px;
    }
  }

  &__list {
    @include list-reset;
    padding-left: 30px;
    padding-right: 30px;

    display: flex;

    flex-wrap: nowrap;
  }

  & li {
    margin-left: 6px;
    margin-right: 6px;
    border: 1px solid rgba(0, 0, 0, 0.12);
    border-radius: 12px;

    @media (max-width: 767px) {
      margin-left: 5px;
      margin-right: 5px;
    }
  }

  & a {
    position: relative;
    display: block;
    padding: 10px 40px;
    font-size: 16px;
    line-height: 21px;
    text-decoration: none;
    transition: color 0.3s;

    @media (max-width: 767px) {
      padding: 10px 26px;
      font-size: 12px;
      line-height: 16px;
    }

    &:hover {
      color: $orange;
    }
  }

  a.catalog-menu__link-svg {
    padding-left: 62px;

    & svg {
      position: absolute;
      left: 34px;
    }

    @media (max-width: 767px) {
      padding-left: 45px;

      & svg {
        left: 25px;
        width: 14px;
        height: 14px;
      }
    }
  }
}
