.delivery {
  background-color: $delivery-background;

  .wrapper {
    width: 86%;
    margin: 0 auto;
    font-family: $font;
    max-width: 1440px;

    & h1 {
      font-size: 46px;
      font-weight: 600;
      margin: 0;
      padding: 55px 0 54px 6px;

      @media (max-width: 600px) {
        font-weight: bold;
        font-size: 20px;
        line-height: 26px;
        padding: 25px 0 20px;
      }
    }

    & h2 {
      font-size: 30px;
      font-weight: 600;
      line-height: 23px;
      margin: 60px 0 40px;

      @media (max-width: 600px) {
        font-size: 16px;
        line-height: 21px;
        padding: 0;
        margin: 30px 0 20px;
      }
    }

    & .card {
      display: flex;
      background-color: $white;
      border-radius: 24px;

      @media (max-width: 1000px) {
        flex-direction: column;
      }

      & .card-info {
        padding: 51px 38px 43px;

        @media (max-width: 600px) {
          padding: 20px 20px 23px;
        }

        & h2 {
          margin: 0;
        }

        & .description {
          color: $delivery-grey;
        }

        & p {
          font-size: 16px;
          font-weight: 400;
          line-height: 24px;
          color: $black;
          padding: 13px 0 16px;

          @media (max-width: 600px) {
            font-size: 14px;
            padding-top: 0px;
          }
        }

        & .pay {
          display: flex;
          align-items: center;
          color: $black;

          & img {
            width: 55px;
            height: 40px;
            padding-right: 16px;

            @media (max-width: 600px) {
              padding-right: 8px;
            }
          }

          & p {
            padding: 0;
            margin: 0;
          }
        }

        & .pay + .pay {
          margin-top: 14px;
        }

        & span {

          & p {
            padding-top: 40px;
            margin: 0;
          }
        }

        & a {
          text-decoration: none;
          font-size: 20px;
          font-weight: 600;
          color: $delivery-orange;

          @media (max-width: 600px) {
            font-size: 18px;
          }
        }
      }

      & .card-image {
        flex: 0 0 68%;

        @media (max-width: 1000px) {
          height: 300px;
        }

        @media (max-width: 600px) {
          height: 200px;
        }

        & img {
          width: 100%;
          height: 100%;
          object-fit: cover;
          border-radius: 0 24px 24px 0;

          @media (max-width: 1000px) {
            border-radius: 0 0 24px 24px;
          }
        }
      }
    }

    & .contacts {
      padding-bottom: 60px;

      

      @media (max-width: 600px) {
        padding-bottom: 30px;
      }

      & .contacts-wrapper {
        display: flex;
        align-items: center;
        border-radius: 12px;
        background-color: $white;
        width: 520px;

        @media (max-width: 600px) {
          width: 100%;
          margin: 0 auto;
          padding: 10px 5px 10px;
        }

        & img {
          margin: 30px;

          @media (max-width: 600px) {
            width: 50px;
            width: 50px;
            margin: 10px;
          }
        }

        & h3 {
          font-size: 18px;
          font-weight: 600;
          margin: 0;

          @media (max-width: 600px) {
            font-size: 14px;
          }
        }

        & p {
          font-size: 14px;
          color: $delivery-subtitle-grey;
          margin: 12px 0 8px;

          @media (max-width: 600px) {
            font-size: 10px;
            margin: 6px 0 2px;
          }
        }

        & a {
          text-decoration: none;
          font-size: 14px;
          color: $delivery-dark-orange;

          @media (max-width: 600px) {
            font-size: 12px;
          }
        }
      }
    }
  }
}
